import React, { Component } from 'react';
import Track from '../Track/Track';
import Card from '@material-ui/core/Card';
import $ from 'jquery';

class RecentlyPlayed extends Component{
    constructor(){
        super();
        this.state = {
            recents: null
            }
    }
    componentDidMount(){
        this.getPrevData();
        this.getEachPrev();
    }
    //call API to get data and sets to state
    getPrevData(){
        $.ajax({
            url: "https://api.spotify.com/v1/me/player/recently-played",
            type: "GET",
            beforeSend: (xhr) => {
                xhr.setRequestHeader("Authorization", "Bearer " + this.props.token);
            },
            success: (data) => {
                this.setState({
                    recents: data.items
                })
            }
        })
    }

    // onHover={this.state.prev.track.preview_url}
    getEachPrev(){
        const prevPlayed = this.state.recents ? (
            this.state.recents.map(prev => {

                return(
                    
                    <Card>
                    <b><p>{prev.track.name}</p></b>
                    <p>{prev.track.artists[0].name}</p>
                    </Card>
                )

            })

        ) : (
            
            <div> No Recent Replays </div>
            
        )

        return prevPlayed;
    }
    render(){
        return(
            <div>
                <h2>Recently Played</h2>
                {this.getEachPrev()}
            </div>
        )
    }
}

export default RecentlyPlayed;